<template>
	<!-- Main Area -->
	<div class="main-area">
		<section class="track-section">
			<div class="single-track-pic" :style="{ backgroundImage: `url('${'/media/'+singleTrack.album_cover}')` }"></div>
			<div class="track-section-title">
				<h4>{{singleTrack.title}}</h4>
				<div>
					<span><b>Album {{singleTrack.album}}</b></span>
					<br>
					By <span v-for="(artist, i) in singleTrack.artists" :key="i" :index="i">{{artist}}</span>
				</div>
			</div>
			<button class="btn" @click="addAndPlayTrack(singleTrack)">Play</button>
			
		</section>
	</div>
</template>

<script>
import { bus } from '@/main';
const axios = require('axios');
export default {
	name: 'Track',
	data: function(){
		return{
			singleTrack: [],
		}
	},
	computed: {
		currentTrackId: function(){
			return this.$store.state.currentTrackId
		}
	},
	methods: {
		addAndPlayTrack: function(e){
			this.$store.dispatch('addQueue', e);
			this.$store.dispatch('selectTrack', e.slug);
			bus.$emit('scrollToActive');
		},
	},
	mounted(){
		axios.get('/api/tracks/' + this.$route.params.slug + '/?format=json').then(response => (this.singleTrack = response.data))
	},
	watch:{
		$route (){
			axios.get('/api/tracks/' + this.$route.params.slug + '/?format=json').then(response => (this.singleTrack = response.data))
		}
	}
}
</script>

<style scoped lang="scss">
$font1: 'Ubuntu Mono', monospace;

/* Color */
$black: rgb(20, 20, 20);
$white: #fff;

@mixin transition($time){
    -o-transition: ease-in-out all $time;
    -moz-transition: ease-in-out all $time;
    -webkit-transition: ease-in-out all $time;
    transition: ease-in-out all $time;
}

@mixin respond-to($breakpoint) {
    @if $breakpoint=="xs" {
        @media (max-width: 567px) {
            @content;
        }
    };
    @if $breakpoint=="sm" {
        @media (max-width: 767px) {
            @content;
        }
    };
    @if $breakpoint=="md" {
        @media (max-width: 991px) {
            @content;
        }
    };
    @if $breakpoint=="lg" {
        @media (max-width: 1200px) {
            @content;
        }
    };
}
.single-track-pic{
	width: 85px;
	height: 85px;
	background-position: center;
	background-size: cover;
	margin-right: 15px;
	border-radius: 4px;
}
.track-section{
	margin-top: 25px;
	margin-bottom: 25px;
	display: flex;
	align-items: flex-start;
	.track-section-title{
		margin-bottom: 30px;
		margin-right: 30px;
		display: flex;
		flex-direction: column;
		h3{
			font-size: 24px;
			font-weight: 700;
			margin-right: 15px;
			margin-top: 0px;
		}
	}
}
</style>
